.button-wrapper {
  justify-content: flex-end;
  align-items: center;
  margin: 0 30px;
  padding: 10px 0 50px;
  display: flex;
}

button {
  text-transform: uppercase;
  color: rgba(96, 94, 94, .62);
  background-image: linear-gradient(to right, #fbc2eb 0%, #a6c1ee 51%, #fbc2eb 100%);
  background-size: 200%;
  border: none;
  border-radius: 10px;
  justify-content: flex-end;
  align-items: center;
  padding: 25px;
  transition: background-image 9s, background-position 9s;
  display: flex;
  box-shadow: 0 0 20px #eee;
}

button:hover {
  background-color: #ff0;
}

